import React, { useState, useEffect} from 'react';
import Header from "./components/header/header"
import {english,spanish} from "./lang"
import ButtondD from './components/download';
import './App.css';

function App() {

  const link_download="https://addons.mozilla.org/es/firefox/addon/betterchat/"
  const [language, translade] = useState({})
  
  useEffect(()=>{
    if(navigator.language==="es-ES"){
      console.log("es")
      translade(spanish)
    }else if(navigator.language==="en-US"){
      translade(english)
    }
  }, [])

  return (
    <div>
      <Header lang={language}></Header>

      <div className="body_container">
         <div className="button_text_container"> 
          <div className="body_text">
            
            <h1>
              {language.title}
            </h1>
            <p>
              {language.main_text}
            </p>
          </div>
          <div className="buttons_container">
            <ButtondD link ="https://chat-bot.imfast.io/chat_server/BetterChatInstaller.exe" text={language.download} desc={language.version}></ButtondD>  
            <ButtondD link = {link_download} text="Firefox AddOn" desc="Version 2.5"></ButtondD>  
          </div>
        </div>
      <div className="imgs_container">
        
      </div>
      </div>
    </div>
  );
}

export default App;
