import React, { useState } from 'react';

const button_d = ({text,desc,link}) =>{
    const link_href=()=>{       
        window.location.href=link
    } 
    return(
        <div className="button" onClick={link_href}>
            <p>
                {text}
            </p>

            <span>
                {desc} 
            </span>
        </div>
    )
}

export default button_d