import React from 'react';
import Txt from "./header_text"
import logo from "./logo-96.png"
const header_comp = ({lang}) =>{
    return (    
    <header>
        <div className="header_container">
            <div className="logo_container">
                <div className="logo">
                    <img src={logo} classname="img" alt="Logo"></img>
                </div>
            </div>

            <div className="links_container">
                <Txt text="Github Repo" link="https://www.github.com/juarezgonz02/better_chat"></Txt>
                <Txt text={lang.about} link="https://www.github.com/juarezgonz02"></Txt>
            </div>
        </div>
    </header>)
        
}
export default header_comp;